<template>
  <el-row :gutter="20">
    <el-col :span="18" :offset="3">
      <p style="margin-bottom: 50px;line-height: 150%;">
        <a href="https://github.com/i2dou/sponge" target="_blank">sponge</a>
        is a powerful golang productivity tool that integrates
        <span class="key-word">automatic code generation</span>,
        <span class="key-word">web and microservice framework</span>,
        <span class="key-word">basic development framework</span>.
        sponge has a wealth of generating code commands, generating different functional code can be combined into a complete service (similar to the way that artificially broken sponge cells can automatically recombine into a new sponge). The code is decoupled and modularly designed, it is easy to build a complete project from development to deployment, so that you develop web or microservices project easily, golang can also be "low-code development".
      </p>
      <p>
        github:
        <a href="https://github.com/i2dou/sponge" target="_blank"
          >https://github.com/i2dou/sponge</a
        >
      </p>
      <p>
        documentation:
        <a href="https://go-sponge.com" target="_blank"
          >https://go-sponge.com</a
        >
      </p>
      <br />
      <p>sponge generates code framework diagrams.</p>
      <div class="home-image">
        <el-image :src="homeImageSrc"></el-image>
      </div>
      <br />
      <p>Microservice service framework diagram.</p>
      <div class="microservices-image">
        <el-image :src="mircoServiceFrameworkImageSrc"></el-image>
      </div>
      <br />
      <p></p>
    </el-col>
  </el-row>
</template>

<script>
export default {
  name: "Home",
  data() {
    return {
      homeImageSrc: require("@/assets/sponge-framework.png"),
      mircoServiceFrameworkImageSrc: require("@/assets/microservices-framework.png"),
    };
  },
};
</script>

<style>
.home-image {
  margin: auto;
}

.microservices-image {
  width: 70%;
  margin: auto;
}

.key-word {
  /* background-color: #ddd; */
  color: #c7254e;
}
</style>



